@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  width: 100vw;
  height: 100vh;
  /* background-color: black; */
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'Lato', sans-serif;
}
button{
  outline: none;
}
.container{
  width: 450px;
  height: 500px;
  background-color: white;
  display: flex;
  flex-direction: column;
  border-radius: 25px;
  box-shadow: 2px 5px 5px rgba(0, 0, 0, 0.4);
}
.wrapper{
  width: 100%;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #eb144c;
  color: white;
  
}
.wrapper:nth-child(1){
  border-radius: 25px 25px 0px 0px;
}
#time-left{
  height: 150px;
  font-size: 130px;
}
.controls{
  height: 150px;
  width: 100%;
  display: flex;
  justify-content: space-evenly;
}
#timer-label{
 font-size: 30px;
 font-weight: 600;
 letter-spacing: 2px;
}
.wrapper button{
  margin: 10px;
  font-size: 25px;
  padding: 10px 20px;
  background-color: transparent;
  border-style: none;
  cursor: pointer;
  color: white;
}

.control-btn{
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.control-btn h2{
  padding: 20px;
  font-weight: 600;
  font-size: 20px;
}

.control-btn button{
  font-size: 18px;
  background-color: #eb144c;
  color: white;
  width: 25px;
  height: 25px;
  border-radius: 30px;
  border-style: none;
  cursor: pointer;
}
.control-btn span{
  font-size: 20px;
  margin: 0px 30px;
}
